import React, {useState} from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Bonus } from '../Bonus/Bonus';
import './OtherBonuses.css'
import { HiMiniChevronDown } from "react-icons/hi2";

interface BonusItem {
  img: string;
  name: string;
  balance: string;
}

interface OtherBonusesProps {
  bonusList: BonusItem[];
}


export default function OtherBonuses({bonusList}: OtherBonusesProps) {
  const [visibility, setVisibility] = useState(false)

  return (
    <>
    <div className='OtherBonusesWrapper' style={{display: visibility ? 'none' : 'flex', transition: 'all .2 easy-in'}} onClick={() => {setVisibility(true)}}>
            <div className='OtherBonusesImages' >
              <div className='OtherBonusesImage OtherBonusesImage1'>
                <img src={bonusList[0].img} alt=""/>
              </div>
              <div className='OtherBonusesImage OtherBonusesImage2'>
                <img src={bonusList[1].img} alt=""/>
              </div>
            </div>
          <p className='OtherBonusesTitle'>Другие активы</p>
          <HiMiniChevronDown style={{marginLeft: 10, fontSize: 18}}/>        
      </div> 
     
    {
      visibility ? (
        bonusList.map((item) => (
          <Bonus img={item.img} name={item.name} balance={item.balance}/>
        ))
      ) : (<></>)
    }

    </>
  );
}